const descriptionFr = ["Jeune garon du Royaume d'Hyrule. Il est ami avec la princesse Zelda depuis qu'ils sont tout petits.", "Jeune garon parti  l'aventure avec Exelo pour reforger l'Epe Sacre afin de sauver la princesse Zelda change en pierre par un Sorcier.", "Princesse du Poyaume d'Hyrule. Elle est toujours gaie et pleine de vie. Elle est amie d'enfance avec Link. Elle sort trs souvent du chteau en cachette pour aller s'amuser, ce qui cause beaucoup de soucis au Ministre du Roi qui est charg de la surveiller.","Etre trange qui ressemble  s'y mprendre  un chapeau.Il recherche un moyen de briser la maldiction d'un puissant sorcier. Il n'est pas trs aimable et traite Link comme un gamin. Mais au fond de lui, il l'aime beaucoup.","Vaati sous la forme malfique qui lui a donne le chapeau fabriqu par Exelo. Il veut s'emparer de la Force pour devenir l'tre le plus puissant.","Roi d'Hyrule. Jadis, c'tait une fine lame.Il a particip au tournoi de la fte des Minish quand il tait jeune.Il a affront Smith lors de ce tournoi mais ils n'ont pas pu se dpartager.","Ministre et pilier du Royaume d'Hyrule. C'est aussi le prcepteur de Zelda. Elle lui donne du fil  retorde  tout le temps sortir du chteau en cachette.","C'est le plus grand forgeron d'Hyrule. Il tait aussi trs dou en escrime. C'est un proche du Roi qui tait son rival  l'pe dans leur jeunesse.","Maire de la Cit d'Hyrule. C'est un fana de masques dont il fait une collection. Il a fait construire un abri sous son jardin en cas d'invasion de monstres.","Deuxime employ de la poste, engag pour aider Stamp qui a trop de travail. Antithse de son collgue, il aime prendre son temps et est un peu lent.","Postier trs srieux qui effectue ses nombreuses tches avec rapidit. Il n'utilise qu'une certaine catgorie de timbres avec lesquels il travaille mieux.","Cordonnier de la Cit d'Hyrule. Il fabrique des chaussures en dormant. Ses chaussures sont rputes pour leur confort et leur finition impeccable. La princesse Zelda fait d'ailleurs partie de sa fidle clientle.","Savant peu aimable qui voue sa vie  la recherche sur les Minish. Aveugl par la passion, il ne se rend pas compte qu'il y en a sous son toit.","Patron de la Galerie Tendo et meilleur sculpteur de figurine au monde. Il collectionne les Coquillages surprise.","Patron de Paradis des Coffres. Frre cadet de Bill. Il rve d'une vie quilibre, sans vice, mais il tient un tablissement de jeux.","Patron de \"Chez Fouyaya\", le bazar de la Cit d'Hyrule. Il est trs occup et dplore de ne pas avoir assez de temps  passer avec son petit chien Brutus.","Patron du Hall des Monstres. Son tablissement a beaucoup de succs car il propose une attraction qui donne l'impressein de combattre de vraies monstres dans un vrai donjon.","Il aimerait louer sa maison en ville, mais son arrogance loigne rapidement les clients potentiels. Il ne se rend pas compte de son caracte et se demande pourquoi tout le monde l'vite.","Enne a un levage de cocottes en ville. Elle offre une rcompense  celui qui voudra bien l'aider  rattraper les cocottes qui se sont chapes.","Le marchand de lgume de la ville. Ses lgumes sont toujours trs frais. Il se dispute toujours avec sa voisine Anasna pour savoir ce qui est le plus dittique entre fruits et lgumes.","La marchande de fruits de la ville. Elle n'a pas son pareil pour la crie. Elle dteste les lgumes et ne vend pas de pastques qu'elle ne considre pas comme des fruits.","Drle de personnage trs dou pour trouver du Nectar Minish bien qu'il ne soit pas un enfant. Il a galement un don pour baratiner les clients afin qu'ils lui en achtent beaucoup.","Facteur trs consciencieux qui distribue le courrier toujours  l'heure.","Ermite vivant au Mont Gonggle. Il a plein de Fragments de Bonheur. Il est trs fier d'avoir jadis remport le tournoi de la fte des Minish.","Vieille dame qui s'est installe sans permission dans la maison de Poemoon, sur le plateau du Blet. Elle refuse d'clairer. Que peut-elle bien cacher ?","Gardien du cimetire de la Valle des Rois. Il communique avec les esprits. Il leur propose des assemblages de fragments qu'il trouve parfois en creusant des tombes.","Esprit d'un ancien roi d'Hyrule qui s'inquite toujours pour son royaume. Il tait en trs bon termes avec les Eoliens, peupele qui vivait jadis sur le site o se tiennent maintenant les Ruines du Vent.","Sorcire qui vit dans la Fort de Tyloria. Elle vends des mixtures et des objets aux pouvoirs tranges. elle cherche une apprentie  qui transmettre les secrets de sa magie.","Grande Fe de la Fort de Tyloria. Elle peut agrandir votre Bourse.","Grande Fe de Mont Gonggle. Elle peut largir votre Sac de Bombes.","Grande Fe de la Valle des Rois. Elle peut agrandir votre Carquois.","Pote qui vit sur le plateau du Blet. Il est de retour d'un long voyage. A son grand dsarroi, il a retrouv sa maison occupe par une inconnue.","Elle est venue s'installer dans la Cit d'Hyrule o elle recherche une maison. Elle serait la descendante d'une prtresse de la rgion de Labrynna.","Elle est venue s'installer dans la Cit d'Hyrule o elle recherche une maison. Elle est toujours joyeuse et ne sait pas refuser un service. Elle s'en veut un peu pour a car elle a parfois l'impression de se faire avoir.","Elle est venue s'installer dans la Cit d'Hyrule o elle recherche une maison. C'est une clbre danseuse de la rgion d'Holodrum.","Papillons trs rares qui portent bonheur  ceux qui les capturent. Si jamais vous en rencontrez un, attrapez-le sans hsiter!","Ghihi pacifique qui cherche  faire des assemblages de Fragments de Bonheur. Il en possde d'ailleurs plusieurs.","Prtre du village des Minish de la Fort de Tyloia. Il a une passion pour les humains dont il parle un peu le langage. C'est pour cela qu'il a tout de suite compris que Link en tait un.","Ancien des Minish sylvestres. Il vit depuis trs longtemps dans le monde des humains. C'est un grand rudit connaissant mme l'emplacement des 4 Elments. C'est le frre jumeau de Booka, l'Ancien des Minish citadins.","Comme tous les autres Minish, ils sont invisibles aux adultes. Leur passion est de faire plaisir aux humains pour lesquels ils fabriquent de nombreux objets trs pratique qu'ils dposent prs de la ville.","Ancien des Minish citadins. Il vit dans un livre. C'est le frre jumeau de Genta, l'Ancien des Minish sylvestres. Sa plume appartenait  une cocotte qu'il a battue en duel tant jeune.","Minish qui ont quitt la fort pour la ville par passion des humains. Ils font tout pour leur rendre service mais il arrive que a les drange...","Le spcialiste des mtaux! Il vit dans le Mont Gonggle avec ses 7 apprentis. Il est un peu rude, mais il a un grand sens du devoir.","Minish qui ont quitt la fort pour aller dans la montagne au service de Melta. Ils ont une chanson originale qui est le symbole des vrais Minish montagnards.","Peuple se nourrissant de pierres et de fer qui vivait dans le Mont Gonggle. Leur nombre est maintenant trs rduit et ils vivent dans une grotte secrte.","Vaati, sous sa forme normale de Minish, avant sa transformation en sorcier. Il tait fascin par le ct sombre du coeur des humain.","Les fidles serviteurs du Roi d'Hyrule. Le rgne bon et juste de leur seigneur fait d'eux des gens aimables et droits.","La Bibliothque Royale d'Hyrule, trs prise des habitants de la ville. Ses employs ont du mal a retrouver les livres qui les gens oublient de rendre.","Matres d'escrime qui ont tous remport le tournoi de la fte des Minish. Conscient du potentiel de Link, ils acceptent de lui apprendre de nouvelles techniques  condition qu'il passe avec brio certaines preuves.","Le couple de boulangers d'Hyrule. Leur Pain Surprise est trs apprsi. C'est un pain qui contient parfois un petit cadeau surprise.","L'cole des enfants d'Hyrule, qui tait aussi celle de Zelda et Link. Les institutrices sont deux jumelles qui s'appellent Timine et Mitine.","Lieu de repos des habitants de la ville et creuset de nombreuses rumeurs. On peut y entendre les clients raconter leur vie ou de prcieux renseignements.","Elle offre des cadeaux aux gens qui dorment dans son auberge. Il y a une salle de repos  l'tage.","Jill est au centre. Il se promnent toujours ensemble. Ils connaissent trs bien la ville et ont parfois des infos interessantes.","Le svre mais comptent matre charpentier et ses apprentis. Les apprentis ont l'air virils mais ont des intonations assez fminines.","Rex et Flicia. Ils sont voisins. Ils voudraient se marier, mais leurs animaux de compagnie se dtestent.","Srie n1 de la collection \"Le paisible peuple d'Hyrule\".","Srie n2 de la collection \"Le paisible peuple d'Hyrule\".","Srie n3 de la collection \"Le paisible peuple d'Hyrule\".","C'est l'animal prfr des habitants de la ville avec sa belle crte rouge. Elles sont gentilles, mais il ne faut pas trop les embter... Les poussins raffolent des vers de terre.","Petite ferme situe prs de la ville et tenue par Talon et sa fille Malon. Malon va vendre son bon lait en ville o il a beaucoup de succs.","Le peuple du vent. Ils ont construit ce qui est devenu les Ruines du Vent. Ils matrisent les vents et vivent maintenant au-dessus des nuages.","Vieil homme que l'in peut rencontrer grce  un assemblage de fragments. Il avait un Pot Magique tant jeune. Il semble hant par un fantme.","Tingle et ses frres, les jumeaux Dingle (en violet) et Klingle (en bleu). Ils esprent pouvoir rencontrer des fes en faisant des assemblages. Ils notent sur une carte tout ce qu'ils savent concernant les fragments. Ils ressemblent  David Junior, qui s'habille en blanc, mais ils ne sont pas frres.","Ils cultivent de superbes lgumes. Rocoli est leur meilleur client.","Il apparat lorsqu'un assemblage est russi pour vendre des fragments. Ses prix sont un peu levs mais il possde des fragments assez rares.","Fantmes de la Valle des Rois. Face a un chapeau bleu et Cieux un rouge. Face est un peu dprim car il n'arrive pas  faire peur aux gens...","Habitat : Donjon Minish. Ils tombent subitement du plafond. Ils sont trs lents. Il suffit de garder son calme et de les battre un par un.","Habitat : Chemin et donjons Minish. Ils attaquent en laant leurs pinces. Sans elles, ils sont plus vulnrables. Approchez-vous en les esquivant et contre-attaquez.","Habitat : Chemin et donjons Minish. Ils surgissent hors du sol quand ils dtectent une proie. S'ils vous avalent, votre corps devient marron et attire les Flaps.","Habitat : Donjon. Ils portent une carapace couverte de piquants. Il faut essayer de les retourner avant de les attaquer.","Habitat : Marais de tabanta et Donjons. Elles se mettent en mouvement quand elles reoivent une flche dans l'oeil. Seules les flches peuvent les abattres, mais vous pouvez aussi les contourner.","Habitat : Grottes, entre autres. Elles se cachent sous terre. Elles crachent des graines si vous vous approchez d'elles. Mais elles sont plutt gentilles une fois qu'on discute avec.","Habitat : Ruines du Vent. Statues mcaniques. Les Minish les ont contruites pour les Eoliens il y a trs longtemps. Elles s'animent quand on les approche.","Habitat : Donjons et grottes, entre autre. Leurs mouvements sont imprvisibles. Il vaut mieux les abattres  distance.","Habitat : Endroits divers. Renards voleurs. Ils s'en prennent aux voyageurs et aux caravanes de marchands. Ils ne sont pas trs forts, mais ils sont rapides et suivent leur proie.","Habitat : Valle des Rois et Chteau des Tnbres, entre autres. Ils sont attirs par la lumire. Ils absorbent l'nergie de leur proie.","Habitat : Palais du vent. Ils ont l'apparence d'une momie. Ils ne reculent pas sous les coups. Mieux vaut garder ses distances.","Habitat : Endroits divers. Leur carapasse les protges des coups. Aspirez-les avec le Pot Magique quand ils sont en boule pour les jecter.","Habitat : Donjon. Ils se dplacent rapidement en longeant les murs. Les attaques normales ne leur font rien, mais le Boomerang est efficace.","Habitat : Marais de la Tabanta, entre autres. Chevaliers  lourde armure. Dstabilisez-les  l'aide de votre Bouclier ou de la commande \"Rouler\".","Habitat : Palais du Vent, entre autres. Capitaines Darknut. Ils portent des coups trs puissants. Guettez la faille dans leur dfense.","Habitat : Donjons. Ils s'approchent ds qu'ils vous reprent et vous suivent. Il faut les bloquer contre quelque chose pour arriver  les semer.","Habitat : Endroits divers. Blobs portant un casque de roche. Il faut d'abord briser leur casque avant d'esprer pouvoir les blesser.","Habitat : Endroit devers. Ils sont rapides et imprvisibles. Coincez-les dans un endroit troit pour qu'ils ne puissent plus esquiver.","Habitat : Valle des Rois et Chteau des Tnbres. Monstres qui ressemblent  s'y mprendre  des portes. Ils tentent de vous crasez quand vous vous en approchez.","Habitat : Mont Gonggle, entre autres. Etranges monstres vgtaux volants. Leurs feuilles forment des hlices. Faites-les tomber avec le Pot Magique.","Habitat : Endroits divers. Ils sont protgs par un masque d'acier. Essayez de les attaquer dans le dos ou de leur retirer leur masque.","Habitat : Donjons. Monstres ayant l'apparence d'une main. Elles vous renvoient  l'entre de leur donjon si elles vous attrapent. Guettez le moment o elles tiombent pour esquiver et contre-attaquer.","Habitat : Donjond. Monstres ayant l'apparende d'une main. Elles vous renvoient  l'entre de leur donjon si elles vous attrapent. Attention  ne pas vous faire coincer quand elle attaquent en groupe.","Habitat : Colline de l'Est, entre autres. Elles surgissent du sol pas cinq. Prenez-les de court en frappant la 1re quand elle sort la tte de terre.","Habitat : Donjons. Elles sont souvent en groupe dans les lieux troits. elles courent partout une fois allumes! Mieux vaut les attaquer avec un Arc.","Habitat : Donjons. Monstres explosifs flottant en l'air. Ils se dplacent en suivant un parcours bien dtermin. Il est donc possible de guetter le bon moment pour les abattres  distance.","Habitat : Grottes, donjons, etc. Il immobilise ses proies en les avalant. Dpchez-vous de vous librer ou vous risquez d'y laisser votre bouclier.","Habitat : Grottes, donjons, etc. Like Like dot d'un Rubis sur la tte. Il l'utilise pour attirer les aventuriers avides de richesses et les dvorer.","Habitat : endroits divers. Ils fondent sur leur proie quand ils la reprent. Il est assez simple de les attirer et de les stopper net d'un coup d'pe.","Habitat : Mont Gonggle, entre autres. Rochers gants dvalant la montagne. Il faut avancer avec prcaution car ils rebondissent chaotiquement.","Habitat : Palais du Vent et Chteau des tnbres. Ils sont assez lents mais ils assnent des coups de flau dvastateurs. Approchez-vous d'eux aprs avoir vit leur flau pour contre-attaquer.","Habitat : Endroits divers, cach sous les touffes d'herbes ou les rochers. Ils attendent qu'une proie passe  proximit pour fondre dessus.","Habitat : Fort de Tyloria et autres. Ils chargent leurs ennemis. Leur lance les protge des attaques de front. Attaquez-les par la gauche.","Habitat : Endroits devers. Ils tirent  vue sur leurs ennemis. Approchez-vous d'eux en vous protgeant derrire votre Bouclier.","Habitat : au dessus des nuages. Ils nagent au milieu des nuages. Attaquez-les quand ils sautent hors des nuages.","Habitat : Chemins et donjons Minish. Il y en a des rouges et des bleues. elles ont l'air inoffensives, mais elles sont coriaces avec leur carapace.","Habitat : chemin et donjons Minish. Il en existe des rouges et des bleus. Les bleus, plus forts, portent des objets pour les lcher sur leur proie.","Habitat : Temple de la Fort. Ils dispersent leurs spores partout. Quand leur chapeau est rouge, ils sont insensibles aux attaques. Par contre, le Pot Magique est assez efficace contre eux.","Habitat : Palais du vent et Chteau des tnbres. Leur technique prfr est de lancer des sorts en se tenant  distance. Tentez de deviner o ils se tlportent pour les attaquer par surprise.","Habitat : Palais du vent et Chteau des tnbres. Experts en magie de Feu. Tentez de deviner o ils se tlportent pour les attaquer par surprise.","Habitat : Palais du vent et Chteau des tnbres. Experts en magie de Glac. Attaquez-les par surprise avec du feu auquel ils sont trs sensibles.","Habitat : Donjons. Il flottent dans les airs. Ils ne blessent pas mais ankylosent, vous empchant d'utiliser votre pe.","Habitat : Endroits divers. il en existe des rouges et des bleus. Ils attaquent en crachant des pierres. Ils sont dans tous les jeux de la srie.","Habitat : Inconnu. Octorok lgendaire de couleur or. on dit qu'il crache des ppites d'or mais rien n'est moins sr...","Habitat : Inconnu. Arakon lgendaire de couleur or. Il se comporte comme un araknon normal mais il est beaucoup plus fort.","Habitat : Inconnu. Rope lgendaire de couleur or. Il est plus froce que les autres et fonce sur sa proie sans rpit.","Habitat : Endroits divers. Corbos, c'est le noir. Attention  Taxos : il vous fait perdre des Rubis en vous rentrant dedans.","Habitat : Ciel. Il est juch sur un petit nuage. Il ne bouge pas mais lance des clairs. Aspirez son nuage avec le Pot Magique.","Habitat : Donjons. Les bleus sautent, les rouges lancent des os. Aspirez leur tte avec le Pot Magique, ils chercherons  la remplacer.","Habitat : Divers. Ils sortent de dessous les rochers ou les touffes d'herbe. Ils vous agrippent, vous empchant de bouger. Chassez-les d'un coup d'pe.","Habitat : Endroit divers. Il en existe de diffrentes couleurs. Chacun a sa particularit, mais les projectiles sont efficaces sur tous.","Habitat : Mont Gonggle, entre autres. Il se dplacent en sautant trs haut. Leurs sauts sont imprvisibles. Attaquez-les quand ils sont  terre.","Habitat : Endroits divers. Ils sont dots de lames affilies. Ils se ruent sur les intrus. Certains suivent un parcours prcis.","Habitat : Mont Gonggle, Marais de Tabanta et autres. Ils se dplacent en creusant le sol pour surprendre leur proie. Restez sur vos gardes en avanant cach derrire votre Bouclier.","Habitat : Temple de la Fort et autres. Elle se dplace en zigzaguant. elles se fche en cours partout quand on la frappe sur le nez.","Habitat : Divers. Il se hrissent de pics quand ils se sentent en danger. Ils se dplacent souvent en groupe. Eliminez-les d'un coup avec une Bombe","Habitat : Temple de l'Eau. Le pouvoir de l'Elment Eau l'avait congel. Depuis le temps qu'il est dans la glace, il est affam et avale n'importe quoi.","Habitat : Palais du Vent. La femelle est plus grosse que le mle. Ils guettent les aventuriers venu au palais  la recherche de l'Elment.","Goron lgendaire dont la taille dpasse celle des montagnes. Il est tellement grand que personne n'a jamais russi  le voir en entier.","Habitat : Temple de la Fort. Ce n'est en fait qu'un blob normal. Il est la preuve qu'un pitre adversaire peut s'avrer terrible si on est petit.","Habitat : Grotte de Feu. Dragon du Mont Gonggle. Il se cache dans la lave et brle tout sur le passage de son terrible souffle.","Habitat : Temple du Vent. C'est le gardien du temple. Les Eoliens l'ont construit pour protger le temple des intrus. Il est extrmement rsistant et impossible  dtruire de l'exterieur.","Habitat : Temple de l'Eau. Ce n'est en fait qu'un blob normal. Mais du point de vue d'un Minish, c'est un adversaire redoutable.","Ils se connaissent depuis tout petits grce  l'amiti qui lie Smith et le Roi. Zelda aimerait que Link devienne plus fort pour qu'il puisse la protger.","Exelo tel qu'il tait avant de subir la maldiction de Vaati. C'est un sage trs renomm dans le monde des Minish. Il est pass matre dans l'art Minish de fabriquer d'tranges objets.","Habitat : Chteau des Tnbres. Le plus puissant des Darknuts. Il barre la route  Link qui tente d'interrompre la crmonie de Vaati.","Vaati sous la forme qu'il a prise en acqurant le pouvoir de la Force. Ses sorts et le rayon de l'oeil qui vole autour de lui sont trs puissants.","Nouvelle tape de l'volution de la transformation de Vaati. Il n'a plus rien de sa forme originelle et seule l'Epe de Quatre peut le vaincre.","L'ultime transformation de Vaati : une concentration de tnbres du coeur. Il n'a plus qu'une seule ide en tte : liminer Link."];

