const descriptionEng = ["A young boy who lives in Hyrule. He is close friends with Princess Zelda.","A young boy on a quest with Ezlo to restore the Four Sword.With the sword's power, he hopes to remove the curse on Princess Zelda.","A bright and cheery princess from Hyrule's royal family.She loves to sneak out of the castle to visit her good friend, Link.","A strange creature that looks at first like a cap. He speaks roughly and treats Link like a child, but he actually really likes Link.","When the young Minish donned Ezlo's magic cap, he took this evil form. Now,he searches for the light force in his quest to become all-powerful.","The king of Hyrule. Reported to have been a fine swordsman, he appeared in the Picori Festival tournament as a youth and fought to a draw with Smith.","The supporting pillar of Hyrule. He is also in charge of Princess Zelda's education, so when she goes missing,he gets frustrated.","The finest swordsmith in Hyrule. As a young man, he was a great swordsman.He and King Daltus are friends and enjoyed a pleasant rivalry as youths.","The mayor of the town of Hyrule. He's a big collector of odd masks. He's built a shelter into his garden just in case monsters ever attack Hyrule.","Second in command at Hyrule's post office. She's mellow and laid-back,entirely unlike the high-strung and easily excitable Stamp.","A busy, hardworking postal employee,although he can be a touch edgy.The stamp he uses is his own personal one, so please don't borrow it. Thanks.","The proprietor of Rem's Shoe Store.Using his \"secret technique,\" he makes shoes in his sleep. Princess Zelda is his single biggest source of income.","A gruff-talking academic type who is obsessed with studying the Picori.He doesn't seem to be aware that they are actually living in his own house.","A sculptor of finely crafted figurines.Many consider him the best sculptor in all of Hyrule. He enjoys collecting Mysterious Shells.","Owner of the Chest Mini-Game Shop.Carlov's younger brother. He detests gambling, but he has made the biggest gamble of all: entrepreneurship.","The handy neighborhood gear seller,also called Stockwell the Well-Stocked.He complains that he's been too busy lately to see his dog, Fifi.","His mysterious dungeon-simulation game is very popular in Hyrule. It gives players the illusion of fighting real monsters in real dungeons.","He wants to rent out his house in town,but he's too overbearing to attract a tenant. He doesn't even seem to realize what the problem is...","This young woman tends to her cuccos in Hyrule Town.If you help her gather her loose cuccos, she'll give you a reward.","Hyrule's vegetable vendor. He sells fine produce. He also argues constantly with the fruit vendor, Pina, over the health benefits of vegetables.","Hyrule's fruit vendor. She hates vegetables, so she won't even sell tomatoes, even though they technically ARE fruits. Freak.","Though he is an adult, he is very good at finding Picolyte made by the Minish.And he is very, VERY convincing about their healthful properties.","A very serious mail deliveryman.He continues to make every delivery right on time every day.","A hermit who lives on Mount Crenel.He has lots of Kinstone pieces.He brags that he won the festival tournament when he was younger.","A weird old lady living in Percy's house without his permission.She doesn't want you to turn on the lights. She might actually be a...","The gravedigger at the cemetery.They say he has the power to speak with the dead. He fuses the Kinstones he digs up with the local ghosts.","The spirit of an ancient king of Hyrule who wishes to secure peace in his land from beyond the grave. He was very fond of the people of the Wind Tribe.","A wizard who lives in the Minish Woods.She sells magic items with odd powers.She's looking for an apprentice to whom she can teach her mystic spells.","The Great Butterfly Fairy of Minish Woods. She provides you with a larger wallet, allowing you to carry more Rupees with you.","The Great Mayfly Fairy of Mount Crenel. She provides you with a larger bomb bag, allowing you to carry more bombs with you.","The Great Dragonfly Fairy of Royal Valley. She gives you a larger quiver, allowing you to carry more arrows with you.","A poet who lives in Western Wood.He comes home from a long journey to find his house occupied by an unwanted guest. Poor guy...","She's looking for a house in Hyrule to move into. She is descended from a line of oracles in the land of Labrynna.","She's looking for a house in Hyrule to move into. She's a very helpful person,but people take advantage of her kind nature. This really bothers her.","She's looking for a house in Hyrule to move into. She is a famous dancer from the land of Holodrum.","A rare butterfly said to bring happiness to those who catch it.If you see one on your journeys,try to grab it!","A strange Ghini who wants to fuse Kinstone pieces, which is somewhat peculiar for a monster. She has a lot of pieces, so keep on fusing!","A Minish living in Minish Village. He speaks some human languages. He sees that Link is human with one look.He's a bit of a human enthusiast.","The elder of the Forest Minish. He has lived among humans for a long time. He knows where the four elements are. He has a twin brother in Hyrule's library.","Not visible to the eyes of adults, they delight in making humans happy by hiding helpful items and Rupees under grass and rocks all over the world.","This Town Minish elder is Gentari's twin brother. Those large wings that he is so proud of are trophies from a duel with a cucco as a young Minish.","These Minish like humans so much that they moved from Minish Village into Hyrule itself! They try to make humans happy, but it sometimes backfires.","He lives with his seven apprentices in Mount Crenel, which contains all the mineral riches they will ever need.He seems gruff, but he's trustworthy.","These seven students followed Melari from Minish Village to Mount Crenel.Their song is actually a sign that they are full-fledged Mountain Minish now.","These rock- and iron-eaters once lived on Mount Crenel in Western Hyrule. Now their numbers are few, and they live quietly in a cave.","Before he became a sorcerer, he was a simple Minish. He had always been entranced by the evil that lives in the hearts of men...","These vassals serve the king of Hyrule. They are loyal and diligent.Like the king himself, they are courteous yet frank.","The Royal Hyrule Library. Although the library is well loved, many forget to return their books, causing no end of trouble for the librarians.","All of these self-trained swordsmen have won the fighting tournament at the Picori Festival before. They see great potential in Link.","This couple bakes bread in Hyrule.Their tasty pastries hold a secret...If you're extra lucky, there'll be a helpful item hidden inside!","This is where all the children of Hyrule learn. Link and Princess Zelda studied here, too. The two teachers are twins named Tina and Dina.","This is the best place in town to relax.You can also pick up some good tips,from the latest gossip to observations about the world.","This generous inn gives a gift to every guest who stays the night.Guests can unwind in the lobby on the second floor.","Zill's the one with the drippy nose. He likes to wander around town with his friends. He knows a lot about Hyrule,and he might even give you some tips!","These carpenters may be rough around the edges, but they do good work.They may seem tough, but they're in touch with their feminine side.","Romio and Julietta grew up next door to one another. They're dating now,but they plan to marry once they get their pets' approval.","Carlov's Peaceful Hyrule Scenery Series No. 1","Carlov's Peaceful Hyrule Scenery Series No. 2","Carlov's Peaceful Hyrule Scenery Series No. 3","With a boisterous crow and a cute crest, these feathered friends are the most popular pets in Hyrule.The baby chicks like small bugs.","This small ranch lies just outside Hyrule Town. Father-and-daughter team Talon and Malon run it, and they sell the finest milk in Hyrule!","The people who built the Wind Ruins.They now live above the clouds,suspended by their own magic ability to control the wind.","An old man plagued by an evil spirit.Coming to his aid will bring you benefit in the end, so if you meet him, you must find a way to help him.","Tingle (in green) and his twin younger brothers Ankle (in purple) and Knuckle (in blue). They believe fusing Kinstones will help them meet fairies, so they are recording Kinstone data on their maps.David Jr. is not technically a relative.","The Hyrulean vegetables grown in their fields are exquisitely delicious.Apparently, Brocco sells the best of their crops.","This Goron appears when you successfully fuse certain Kinstone pieces. He will sell you expensive,but rare, Kinstone pieces.","Ghosts from the Royal Valley. The one in the blue cap is Spookter, and the one in the red cap is Spekter. Spookter tries to be scary, but he's just not.","Appears in Minish dungeons.They drop from the ceiling unexpectedly. They are slow, so take your time, and defeat them one by one.","Appears in Minish roads and dungeons.These monsters have sharp mandibles.Hit them when they shoot these away.Avoid their attacks to get in close.","Appears in Minish roads.They come out of the ground when they sense prey. If you get swallowed, you'll take damage and get all dirty! Watch out, because when you're all dirty, Pestos will come after you.","Appears in dungeons.Covered in hard, spiny shells.Few attacks work on them. You have to flip them over to hurt them.","Appears in Castor Wilds.They move when you put an arrow in their eye. No other attacks work.You can always just let them pass...","Appears in caves, etc.Normally stay underground. They'll pop out and spit seeds at you, but if you get to know 'em, they're not so bad.","Appears in the Wind Ruins.Built by the Minish for the Wind Tribe long ago. They look like stone statues,but if you get too close, they move!","Appears in various areas.These bats live in dungeons and caves.Their movement is unpredictable, so use ranged weapons from a safe distance.","Appears in various areas.This thieving fox preys upon travelers and merchants. He may not be strong,but he will attack very quickly,so be careful.","Appears in the Royal Valley, etc.These dark beasties are attracted to light, and if they attack you, they may suck your life away!","Appears in the Palace of Winds.These mummies keep coming at you when you attack. It's better to fight from a distance if you want to avoid damage.","Appears in dungeons.His hard shell protects him from swords,but once he rolls into a ball, you can use the Gust Jar to draw him in.","Appears in dungeons.They cling to walls and move quickly.Normal attacks may not work, but the boomerang is pretty effective.","Appears in Castor Wilds.These armor-clad soldiers are tough.Use your shield and rolling skills to find an opening in their defenses...","Appears in the Palace of Winds.These Dark Nut commanders are strong,but if you relax and wait for your opening, you can still defeat them.","Appears in dungeons.They move quickly to chase you once they spot you. Your shield will block them, but keep on your toes!","Appears in Veil Falls.These tough Chuchus wear rocks on their heads. The rocks protect them from damage, so you'll have to find a way to knock the rocks off before you'll be able to destroy them.","Appears in various areas.These guys move quickly and randomly.Trap them in a corner and take them out quickly.","Appears in the Royal Crypt.They look like doors, but they're really traps! If you get too close to one, it will fall down on you!","Appears on Mount Crenel, etc.These strange beasts hover on propeller-like leaves. You can pull them out of the sky with your Gust Jar.","Appears in various areas.Their fronts are protected with metal masks, but their backsides aren't! Pop off that mask for an easy battle.","Appears in dungeons.If these guys grab you, they'll send you back to the start of the dungeon.Dodge them as they fall, then attack.","Appears in dungeons.If these guys grab you, they'll send you back to the start of the dungeon.Watch out when he attacks with others.","Appears in the Eastern Hills, etc.Five of them pop out at once. If you smack each one as they pop out of the ground, they're a piece of cake.","Appears in dungeons.They often gather in groups in narrow areas. Once activated, they go a little crazy. Take them out with arrows.","Appears in dungeons.They float in the air and explode when touched. Walk carefully, and you may escape unhurt...","Appears in caves, dungeons, etc.Don't let them grab you: they'll eat your shield if they hold you long enough. Swing your sword to escape.","Appears in caves, dungeons, etc.This Rupee Like has a Rupee dangling from its head stalk. This clever lure attracts greedy and delicious heroes.","Appears in various areas.They'll come straight for you if they spot you. Just swing your sword when they charge at you.","Appears on Mount Crenel, etc.These huge boulders come crashing down from cliff walls. They fall in a random pattern, so watch your step!","Appears in the Palace of Winds.They're not fast, but that iron ball is a bruiser! Try to hit them after they swing the ball.","Appears in various areas.They hide under common rocks and grass. Be careful, because they can pop out when you least expect it!","Appears in Minish Woods, etc.They rush you on sight. They also block head-on attacks with their spears, so circle around to attack.","Appears in various areas.They fire arrows at you on sight.Block these with your shield and then close in to attack.","Appears in the sky.They swim through clouds like fish in water. Attack them in that brief moment when they pop out!","Appears in Minish roads, dungeons,etc. There are red and blue ones.They may look small and weak, but their hard shells make them formidable.","Appears in Minish roads, dungeons,etc. There are red and blue ones.The blue ones are stronger and throw things down on your head.","Appears in Deepwood Shrine.These mushroom beasties scatter spores all over dungeons. When their caps are red, they are invincible.","Appears in the Palace of Winds, etc.They fire magic bolts from afar. Hit them when they appear so they don't cast another spell!","Appears in the Palace of Winds.They wield fire magic. Hit them when they appear so they don't cast another spell!","Appears in the Palace of Winds.They wield ice magic. They're weak against fire, so attack with fire for a quick battle!","Appears in dungeons.They float in midair. They won't hurt you, but if you touch them, you won't be able to use your sword for a while.","Appears in various areas.There are red and blue ones. They've appeared in every Zelda game to date.They spit rocks, so be careful!","Appears in... Well, we're not sure! The legendary Golden Octorok. It can't be confirmed, but they are said to shoot chunks of pure gold...","Appears in... Well, we're not sure! The legendary Golden Tektite.Its basic attack is the same,but it has much more power.","Appears in... Well, we're not sure! The legendary Golden Rope. It's much more aggressive than the normal ones: it will attack you on sight.","Appears in various areas.The black one is Crow. If you touch Takkuri, he will steal Rupees from you, so be careful!","Appears in the sky.They float on clouds. They don't move,but they do throw lightning bolts.Steal their clouds with the Gust Jar.","Appears in dungeons.Blue ones jump, and red ones throw bones. See what happens when you remove their heads with the Gust Jar.","Appears in various areas.They appear under rocks and grass.If they grab you, you won't be able to move. Swing your sword to get away.","Appears in various areas.Chuchus come in many colors.Each color is a little different, but ranged weapons work well against all.","Appears on Mount Crenel, etc.They move by jumping long distances.Their movement can be unpredictable,so attack them when they are still.","Appears in various areas.A trap with sharp blades. Some are triggered by nearby intruders, while others follow a set path.","Appears in Wind Ruins, Veil Falls.These baddies move freely underground when attacking. You never know where they will pop up, so keep your shield raised and at the ready!","Appears in Deepwood Shrine, etc.This caterpillar moves in a zigzag pattern. If you hit him on the nose, he gets upset and starts running around.","Appears in various areas.These Chuchus project spikes from their bodies at the first sign of danger. Try a well-placed bomb.","Appears in the Temple of Droplets.Frozen solid by the power of the Water Element. He's been frozen so long that his hunger is unbearable.","Appears in Palace of Winds.Females are larger than males.They fly around the Palace of Winds,preying on adventurers.","This is a legendary Goron with a body bigger than a mountain. This Goron is so big, in fact, that no one has ever seen all of it at once.","Appears in Deepwood Shrine.A perfectly ordinary Chuchu. Though not much of an enemy usually, they are terrifying to anyone Minish-sized.","Appears in the Cave of Flames.A Gleerok with a hard, rocky shell found in Mount Crenel. Hiding in lava, this beast spews fire on everything it sees.","Appears in the Wind Ruins.Constructed by the Wind Tribe to repel intruders, this sturdy machine cannot be destroyed by any external force.","Appears in the Temple of Droplets.An ordinary blue Chuchu. Fighting him while Minish-sized is daunting, but just try to avoid that electric attack.","These two became fast friends because of Smith and the king of Hyrule. Zelda wishes that the sometimes unreliable Link were just a wee bit stronger.","The Minish Ezlo, before he was cursed by Vaati. He is a famous Minish sage.Even among Minish inventors, he was renowned for his amazing creations.","Appears in Dark Hyrule Castle.This is the strongest Dark Nut, with a good balance of attack and defense.They will do anything to stop you.","The sorcerer Vaati took this form after draining the power of the light force from Princess Zelda. The evil beams from his eyes are devastating.","Once Vaati's body has been shattered,this dark form rises up, all that remains of the evil sorcerer. Only the sacred Four Sword can defeat him.","This is the embodiment of purest evil,the final form of the power-mad Vaati.Its mind is consumed with a hunger for destruction. Find its weakness."];

export const Day4 = () => {

    const navigate = useNavigate();

    const [idFigure, setIdFigure] = useState(0);
    const [, setRefresh] = useState(false);
    const [eng, setEng] = useState(true);

    const figurine = useMemo(() => eng ? figurineEng : figurineFr, [eng]);
    const description = useMemo(() => eng ? descriptionEng : descriptionFr, [eng]);

    useEffect(() => {
        const resize = () => setRefresh(r => !r);
        window.addEventListener('resize', resize);

        return () => window.removeEventListener('resize', resize);
    }, []);

    const digit = (i) => {
        const id = idFigure+1;
        if(i === 0)
            return id < 100 ? 0 : 1;
        if(i === 1){
            if(id > 99)
                return id.toString().charAt(1);
            if(id > 9)
                return id.toString().charAt(0);
            return 0;
        }
        return id%10;
    }


    return <>
        <div className="container4">
            <img id="background" alt="bg" src={background}/>

            <img className="arrow" alt="" id="up" src={up} onClick={() => setIdFigure(i => i === 0 ? 0 : i-1)}/>
            <img className="arrow" alt="" id="down" src={down} onClick={() => setIdFigure(i => i === figurine.length - 1 ? i : i+1)}/>

            <div id="description">
                {description[idFigure]}
            </div>

            <div id="figureNames">
                {figurine.map((name, i) => <div key={i} onClick={() => setIdFigure(figurine.indexOf(name))}>
                    {i === idFigure && <img id="selection" alt="" src={selection}/>}
                    <span>{name}</span>
                </div>)}
            </div>

            <img src={process.env.PUBLIC_URL+'/day4/'+(idFigure+1)+'.png'}
                 alt={figurine[idFigure]}
                 style={{'--screen-height': window.innerHeight}}
                 id="figure_img"/>

            <div id="figurine_number">
                <img alt="" src={process.env.PUBLIC_URL+'/day4/nb_'+digit(0)+'.png'}/>
                <img alt="" src={process.env.PUBLIC_URL+'/day4/nb_'+digit(1)+'.png'}/>
                <img alt="" src={process.env.PUBLIC_URL+'/day4/nb_'+digit(2)+'.png'}/>
            </div>

            <div id="language">
                <div onClick={() => setEng(true)}>EN</div>
                <div onClick={() => setEng(false)}>FR</div>
            </div>

        </div>
        <img src={home} alt="" id="home" width={60} onClick={() => navigate('/')}/>

    </>

}
